'use client'

import { useState, useEffect } from 'react'
import LiveIcon from '@/share/icon/live'
import { Circle } from 'lucide-react'
import { cameraFeeds } from '@/data/videos'
import CameraFeed from '@/components/CameraFeed'
import HardwareControl from '@/components/control_hardware'
import GateInfo from '@/components/gate_condition'
import OperationRecord from '@/components/operator'

export default function SecurityCameraGrid() {
  const [activeCamera, setActiveCamera] = useState<number | null>(null)
  
  // Reset active camera after 10 seconds of inactivity
  useEffect(() => {
    if (activeCamera === null) return
    
    const timer = setTimeout(() => setActiveCamera(null), 10000)
    return () => clearTimeout(timer)
  }, [activeCamera])

  const activeCamerasCount = cameraFeeds.filter((cam) => cam.isActive).length

  return (
    <div className="min-h-screen rounded-lg bg-white p-4">
      {/* Header with camera status and live button */}
      <header className="flex items-center justify-between mb-4  text-gray-700">
        <div className="flex items-center gap-2">
          <Circle className="w-2 h-2 fill-green-500 text-green-500" />
          <span className="text-sm">{activeCamerasCount} Camera Active</span>
        </div>
        <button className="hover:bg-gray-800 p-2 rounded-full transition-colors">
          <LiveIcon />
        </button>
      </header>

      {/* Camera grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-2 gap-4">
        {cameraFeeds.map((camera) => (
          <CameraFeed
            key={camera.id}
            camera={camera}
            isActive={activeCamera === camera.id}
            onMouseEnter={() => setActiveCamera(camera.id)}
            onMouseLeave={() => setActiveCamera(null)}
          />
        ))}
      </div>

      {/* Control sections */}
      <div className="mt-16 mx-auto max-w-6xl">
        <div className="flex flex-col md:flex-row justify-between gap-6">
          <HardwareControl
            onUp={() => console.log('Camera up')}
            onStop={() => console.log('Camera stop')}
            onDown={() => console.log('Camera down')}
          />
        </div>
        
        {/* Additional information components */}
        <GateInfo />
        <OperationRecord />
      </div>
    </div>
  )
}