// components/CameraFeed.tsx
'use client'

import { Circle } from 'lucide-react'
import CameraStream from '@/components/camera_frame'
import HLSStream from '@/components/Hls'

interface CameraFeedProps {
  camera: {
    id: number
    videoUrl: string
    isActive: boolean
  }
  isActive: boolean
  onMouseEnter: () => void
  onMouseLeave: () => void
}

export default function CameraFeed({ camera, isActive, onMouseEnter, onMouseLeave }: CameraFeedProps) {
  const baseClassNames = "w-full h-full object-cover rounded-md border-2 border-[#444D56]"

  const renderCameraFeed = () => {
    switch (camera.id) {
      // case 1:
      //   return <CameraStream sseUrl="http://160.22.122.122:8001/sse" className={baseClassNames} />
      // case 2:
      //   return <HLSStream videoUrl="http://160.22.122.122:8080/hls/stream.m3u8" className={baseClassNames} />
      default:
        return (
          <video
            className={baseClassNames}
            controls
            controlsList="nodownload nofullscreen"
          >
            <source src={camera.videoUrl} type="video/mp4" />
            Trình duyệt của bạn không hỗ trợ thẻ video.
          </video>
        )
    }
  }

  return (
    <div
      className="relative transition-transform hover:scale-[1.01]"
      onMouseEnter={onMouseEnter}
      onMouseLeave={onMouseLeave}
    >
      {/* Camera video container */}
      <div className="aspect-video bg-gray-800 rounded-lg overflow-hidden">
        {renderCameraFeed()}
      </div>

      {/* Live indicator overlay */}
      {isActive && (
        <div className="absolute top-4 left-4 right-4 flex justify-end text-white">
          <div className="flex items-center gap-2 bg-black/30 px-2 py-1 rounded-full">
            <Circle className="w-2 h-2 fill-red-500 text-red-500 animate-pulse" />
            <span className="text-sm font-medium">Live</span>
          </div>
        </div>
      )}
    </div>
  )
}